import lvSelect from '@/components/lv-select/lv-select.vue';
import userTopBar from '@/components/activity/userTopBar.vue';
import likeIcon from '@/components/common/commonIcon/likeIcon.vue';
export default {
	components: {
		lvSelect,
		userTopBar,
		likeIcon
	},
	data() {
		return {
			needHeight:0,
			currentIndex: 0,
			nav: ['众投活动', '大佬赞助', '技能需求'],
			needInfo: { userInfo: { college: '上海海事大学',logo:"https://lz.sinaimg.cn/orj1080/967d9727ly3gc0whyfofkj20sg0sg4av.jpg" }
			,like_nums:0, title: '一起来玩啊！', content: '招募一只皮皮虾作为镇宅神兽' },
			loading: false,
			showValue: 'name', // 需要显示的数据，必须与infoList中的name对应
			searchValue: '',
			infoList: [],
			infoLists: [
				{
					name: '吕星辰1'
				},
				{
					name: '吕星辰2'
				},
				{
					name: '吕星辰3'
				},
				{
					name: '吕星辰4'
				}
			]
		};
	},
	methods: {
		changeNav(id) {
			this.currentIndex = id;
		},
		gotoPublishNeed() {},
		handleSearch() {
			this.loading = true;
			setTimeout(() => {
				this.loading = false;
				this.infoList = this.infoLists;
			}, 2000);
		},
		change(val) {
			console.log(val);
		}
	},
	onLoad(){
		uni.getSystemInfo({
			success: res => {
				let height = res.windowHeight - uni.upx2px(145.2);
				this.needHeight = height;
			}
		});
	},
};
